'use client'

/**
 * Secure local storage utilities for PCOS Companion app
 * Implements HIPAA-compliant data handling practices
 */

export interface StorageOptions {
  encrypt?: boolean
  expirationDays?: number
}

export interface StoredData<T = any> {
  data: T
  timestamp: number
  encrypted: boolean
  expiresAt?: number
}

/**
 * Simple encryption/decryption using Web Crypto API
 * Note: This is basic encryption for demo purposes
 * Production apps should use more robust encryption
 */
class SimpleEncryption {
  private static async getKey(): Promise<CryptoKey> {
    const keyMaterial = await crypto.subtle.importKey(
      'raw',
      new TextEncoder().encode('pcos-companion-key-2024'), // In production, use a proper key derivation
      { name: 'PBKDF2' },
      false,
      ['deriveKey']
    )

    return crypto.subtle.deriveKey(
      {
        name: 'PBKDF2',
        salt: new TextEncoder().encode('pcos-salt'),
        iterations: 100000,
        hash: 'SHA-256'
      },
      keyMaterial,
      { name: 'AES-GCM', length: 256 },
      false,
      ['encrypt', 'decrypt']
    )
  }

  static async encrypt(data: string): Promise<string> {
    try {
      const key = await this.getKey()
      const iv = crypto.getRandomValues(new Uint8Array(12))
      const encodedData = new TextEncoder().encode(data)

      const encrypted = await crypto.subtle.encrypt(
        { name: 'AES-GCM', iv },
        key,
        encodedData
      )

      const combined = new Uint8Array(iv.length + encrypted.byteLength)
      combined.set(iv)
      combined.set(new Uint8Array(encrypted), iv.length)

      return btoa(String.fromCharCode(...combined))
    } catch (error) {
      console.error('Encryption failed:', error)
      return data // Fallback to unencrypted
    }
  }

  static async decrypt(encryptedData: string): Promise<string> {
    try {
      const key = await this.getKey()
      const combined = new Uint8Array(
        atob(encryptedData).split('').map(char => char.charCodeAt(0))
      )

      const iv = combined.slice(0, 12)
      const encrypted = combined.slice(12)

      const decrypted = await crypto.subtle.decrypt(
        { name: 'AES-GCM', iv },
        key,
        encrypted
      )

      return new TextDecoder().decode(decrypted)
    } catch (error) {
      console.error('Decryption failed:', error)
      return encryptedData // Fallback to original data
    }
  }
}

/**
 * Secure storage manager
 */
export class SecureStorage {
  private static isClient = typeof window !== 'undefined'

  /**
   * Store data securely in localStorage
   */
  static async setItem<T>(
    key: string, 
    value: T, 
    options: StorageOptions = {}
  ): Promise<boolean> {
    if (!this.isClient) return false

    try {
      const { encrypt = true, expirationDays } = options
      
      const storedData: StoredData<T> = {
        data: value,
        timestamp: Date.now(),
        encrypted: encrypt,
        expiresAt: expirationDays 
          ? Date.now() + (expirationDays * 24 * 60 * 60 * 1000)
          : undefined
      }

      let serializedData = JSON.stringify(storedData)
      
      if (encrypt) {
        serializedData = await SimpleEncryption.encrypt(serializedData)
      }

      localStorage.setItem(`pcos_${key}`, serializedData)
      return true
    } catch (error) {
      console.error('Failed to store data:', error)
      return false
    }
  }

  /**
   * Retrieve data securely from localStorage
   */
  static async getItem<T>(key: string): Promise<T | null> {
    if (!this.isClient) return null

    try {
      const rawData = localStorage.getItem(`pcos_${key}`)
      if (!rawData) return null

      let serializedData = rawData
      
      // Try to parse as StoredData first to check if it's encrypted
      let storedData: StoredData<T>
      try {
        storedData = JSON.parse(rawData)
      } catch {
        // If parsing fails, assume it's encrypted
        serializedData = await SimpleEncryption.decrypt(rawData)
        storedData = JSON.parse(serializedData)
      }

      // Check if data has expired
      if (storedData.expiresAt && Date.now() > storedData.expiresAt) {
        this.removeItem(key)
        return null
      }

      // If data is marked as encrypted but we haven't decrypted yet
      if (storedData.encrypted && serializedData === rawData) {
        serializedData = await SimpleEncryption.decrypt(rawData)
        storedData = JSON.parse(serializedData)
      }

      return storedData.data
    } catch (error) {
      console.error('Failed to retrieve data:', error)
      return null
    }
  }

  /**
   * Remove item from localStorage
   */
  static removeItem(key: string): boolean {
    if (!this.isClient) return false

    try {
      localStorage.removeItem(`pcos_${key}`)
      return true
    } catch (error) {
      console.error('Failed to remove data:', error)
      return false
    }
  }

  /**
   * Clear all PCOS app data
   */
  static clearAll(): boolean {
    if (!this.isClient) return false

    try {
      const keys = Object.keys(localStorage).filter(key => key.startsWith('pcos_'))
      keys.forEach(key => localStorage.removeItem(key))
      return true
    } catch (error) {
      console.error('Failed to clear data:', error)
      return false
    }
  }

  /**
   * Get storage usage statistics
   */
  static getStorageStats(): { totalKeys: number; estimatedSize: number } {
    if (!this.isClient) return { totalKeys: 0, estimatedSize: 0 }

    try {
      const keys = Object.keys(localStorage).filter(key => key.startsWith('pcos_'))
      let estimatedSize = 0

      keys.forEach(key => {
        const value = localStorage.getItem(key)
        if (value) {
          estimatedSize += key.length + value.length
        }
      })

      return {
        totalKeys: keys.length,
        estimatedSize: estimatedSize * 2 // Rough estimate (UTF-16)
      }
    } catch (error) {
      console.error('Failed to get storage stats:', error)
      return { totalKeys: 0, estimatedSize: 0 }
    }
  }

  /**
   * Export all data for backup
   */
  static async exportData(): Promise<string | null> {
    if (!this.isClient) return null

    try {
      const keys = Object.keys(localStorage).filter(key => key.startsWith('pcos_'))
      const exportData: Record<string, any> = {}

      for (const key of keys) {
        const cleanKey = key.replace('pcos_', '')
        exportData[cleanKey] = await this.getItem(cleanKey)
      }

      return JSON.stringify({
        exportDate: new Date().toISOString(),
        version: '1.0',
        data: exportData
      }, null, 2)
    } catch (error) {
      console.error('Failed to export data:', error)
      return null
    }
  }

  /**
   * Import data from backup
   */
  static async importData(jsonData: string): Promise<boolean> {
    if (!this.isClient) return false

    try {
      const importData = JSON.parse(jsonData)
      
      if (!importData.data || typeof importData.data !== 'object') {
        throw new Error('Invalid import data format')
      }

      // Clear existing data first
      this.clearAll()

      // Import new data
      for (const [key, value] of Object.entries(importData.data)) {
        await this.setItem(key, value)
      }

      return true
    } catch (error) {
      console.error('Failed to import data:', error)
      return false
    }
  }
}

/**
 * Specific storage keys used throughout the app
 */
export const STORAGE_KEYS = {
  SYMPTOMS: 'symptoms',
  CYCLE_ENTRIES: 'cycle_entries',
  NUTRITION_PREFERENCES: 'nutrition_preferences',
  USER_SETTINGS: 'user_settings',
  MEAL_PLANS: 'meal_plans',
  FOOD_RATINGS: 'food_ratings'
} as const

/**
 * Type-safe storage helpers for specific data types
 */
export const SymptomStorage = {
  async save(symptoms: any[]): Promise<boolean> {
    return SecureStorage.setItem(STORAGE_KEYS.SYMPTOMS, symptoms)
  },
  
  async load(): Promise<any[] | null> {
    return SecureStorage.getItem<any[]>(STORAGE_KEYS.SYMPTOMS)
  }
}

export const CycleStorage = {
  async save(entries: any[]): Promise<boolean> {
    return SecureStorage.setItem(STORAGE_KEYS.CYCLE_ENTRIES, entries)
  },
  
  async load(): Promise<any[] | null> {
    return SecureStorage.getItem<any[]>(STORAGE_KEYS.CYCLE_ENTRIES)
  }
}

export const NutritionStorage = {
  async save(data: any): Promise<boolean> {
    return SecureStorage.setItem(STORAGE_KEYS.NUTRITION_PREFERENCES, data)
  },
  
  async load(): Promise<any | null> {
    return SecureStorage.getItem(STORAGE_KEYS.NUTRITION_PREFERENCES)
  }
}
