interface FoodItem {
  name: string
  score: number
  category: string
  benefits: string[]
  concerns: string[]
  alternatives: { name: string; score: number }[]
}

interface MealPlan {
  id: string
  name: string
  description: string
  meals: {
    breakfast: string[]
    lunch: string[]
    dinner: string[]
    snacks: string[]
  }
  nutritionFocus: string[]
}

interface Recipe {
  id: string
  name: string
  category: string
  prepTime: number
  servings: number
  ingredients: string[]
  instructions: string[]
  nutritionBenefits: string[]
  pcosRating: number
}

